/**
* Post Grid Editor + FrontEnd Styles
*
* CSS for both Frontend+Backend.
*/

$columns: 5;
.gmfgb-mg-grid {
	display: flex;
	flex-wrap: wrap;
	margin: 0;
	font-size: 0;
	position: relative;
}

.gmfgb-mg-fancy-custom {
	.fancybox-video,
	.fancybox-image {
		position: static !important;
	}
}

.image-caption {
	position: absolute;
	left: 50%;
	// bottom: 0;
	padding: 12px;
	background-color: rgba(0, 0, 0, 0.55);
	z-index: 2;
	font-size: 16px;
	color: #fff;
	width: 88%;
	overflow: auto;
	transform: translateX(-50%);
	text-align: center;
	border-radius: 5px;
	max-height: 100%;
}
.gmfgb-mg-media {
	position: relative;
	padding: 0;
	box-sizing: border-box;
	font-size: 0;
	// border-radius: 20px;
	overflow: hidden;
	.main-class {
		&.true {
			img {
				width: 100%;
				filter: grayscale(1);
			}
		}
	}
	> a {
		display: block;
	}
	img {
		width: 100%;
	}
	svg {
		background: #ffffff;
		padding: 5px;
		position: absolute;
		top: 20px;
		left: 20px;
		// border-radius: 100px;
		z-index: 4;
	}
}
.main-class.top .image-caption {
	position: absolute;
	top: 7px;
}
.main-class.bottom .image-caption {
	position: absolute;
	bottom: 7px;
}
.main-class.center .image-caption {
	position: absolute;
	display: flex;
	justify-content: center;
	align-items: center;
	top: 50%;
	transform: translate(-50%, -50%);
}
.main-class .image-caption p {
	margin: 0;
}

.main-class.true {
	position: relative;
}
.main-class.true::before {
	content: "";
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	transition: 0.4s;
	opacity: 0;
	visibility: hidden;
	pointer-events: none;
}